html,
body {
    margin: 0 auto;
    overflow: hidden;
    user-select: none;
    font-family: Arial, Helvetica, sans-serif;

    .overlay {
        position: absolute;
        top: 0;

        #hideControlsBtn {
            border: none;
            background-color: #333;
            color: #fff;
            padding: 10px 15px;
            border-radius: 5px;
            opacity: 0.2;
            transition: 100ms;
            cursor: pointer;

            &:hover {
                opacity: 1;
            }

            &:active {
                background-color: #222;
            }
        }

        #controls {
            display: none;
            flex-direction: column;
            color: white;
            padding: 10px;

            .control {
                display: inline-flex;
            }

            p {
                margin: 5px;
                width: 50px;
            }
        }
    }
}
